@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
.base-container{width:100%;display:flex;flex-direction:column;align-items:center;margin-top:100px}.base-container .register{margin-top:190px}.base-container .header{font-size:24px;font-family:"Nerko One", cursive}.base-container .content{display:flex;flex-direction:column}.base-container .content .image{width:21em}.base-container .content .image img{height:100%;width:100%}.base-container .content .form{display:flex;flex-direction:column;align-items:center}.base-container .content .form .form-group{display:flex;flex-direction:column;align-items:flex-start;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.base-container .content .form .form-group label{font-size:20px}.base-container .content .form .form-group input{margin:6px;min-width:18em;height:37px;padding:0px 10px;font-size:16px;font-family:"Courier New", Courier, monospace;background-color:#f3f3f3;border:0;border-radius:4px;margin-bottom:31px;transition:all 250ms ease-in-out}.base-container .content .form .form-group input:focus{outline:none;box-shadow:0px 0px 12px 0.8px #0e81ce96}.base-container .footer{margin-top:3em}

.LoginHome{text-align:center;display:flex;justify-content:center;align-items:center;font-family:"Open Sans", sans-serif}.login{width:27em;height:40em;display:flex;justify-content:center;align-items:center;margin-top:5px;position:relative;z-index:99}.login .container{display:flex;justify-content:center;align-items:center;background-color:#fff;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.2);border-radius:4px;position:relative;z-index:99;width:100%;height:auto;z-index:99;padding:17px 10px;margin-top:100px}.login .right-side{display:flex;flex-direction:column;justify-content:center;height:90%;background-color:#7358b3;width:100%;position:absolute;right:-34%;border-radius:6px;z-index:1;transition:all 400ms ease-in-out;cursor:pointer;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.281)}.login .right-side.right{right:-40%;align-items:flex-end}.login .right-side.right:hover{right:-45%}.login .right-side.left{right:40%;align-items:flex-start}.login .right-side.left:hover{right:45%}.login .right-side .text{font-size:21px;font-weight:500;color:#fff;margin-right:3em;margin-left:3em}.btn{font-size:21px;padding:5px 20px;border:0;background-color:#7358b3;color:#fff;border-radius:3px;transition:all 250ms ease-in-out;cursor:pointer}.btn:hover{background-color:#7358b3}.btn:focus{outline:none}

button{justify-self:center;margin:auto;font-size:25px}*,*:before,*:after{box-sizing:border-box;margin:0;padding:0}body{background:#333;font-family:"Open Sans",Helvetica,Arial,sans-serif}.buttons{margin-top:50px;text-align:center}.blob-btn{z-index:1;position:relative;padding:20px 46px;margin-bottom:30px;text-align:center;text-transform:uppercase;color:#0fe0f5;font-size:16px;font-weight:bold;background-color:transparent;outline:none;border:none;transition:color 0.5s;cursor:pointer}.blob-btn:before{content:"";z-index:1;position:absolute;left:0;top:0;width:100%;height:100%;border:6px solid #0fe0f5}.blob-btn:after{content:"";z-index:-2;position:absolute;left:9px;top:9px;width:100%;height:100%;border:6px solid #222;transition:all 0.3s 0.2s}.blob-btn:hover{color:#222}.blob-btn:hover:after{transition:all 0.3s;left:0;top:0}.blob-btn__inner{z-index:-1;overflow:hidden;position:absolute;left:0;top:0;width:100%;height:100%}.blob-btn__blobs{position:relative;display:block;height:100%;-webkit-filter:url("#goo");filter:url("#goo")}.blob-btn__blob{position:absolute;top:6px;width:25%;height:100%;background:#0fe0f5;border-radius:100%;transform:translate3d(0, 150%, 0) scale(1.7);transition:transform 0.45s}@supports ((-webkit-filter: url("#goo")) or (filter: url("#goo"))){.blob-btn__blob{transform:translate3d(0, 150%, 0) scale(1.4)}}.blob-btn__blob:nth-child(1){left:0%;transition-delay:0s}.blob-btn__blob:nth-child(2){left:30%;transition-delay:.08s}.blob-btn__blob:nth-child(3){left:60%;transition-delay:.16s}.blob-btn__blob:nth-child(4){left:90%;transition-delay:.24s}.blob-btn:hover .blob-btn__blob{transform:translateZ(0) scale(1.7)}@supports ((-webkit-filter: url("#goo")) or (filter: url("#goo"))){.blob-btn:hover .blob-btn__blob{transform:translateZ(0) scale(1.4)}}

